export default {
  
  data () {
    return {
      number: 0,
      list: [],
      isFinish: false
    }
  },
  onLoad () {
    this.getData()
    uni.showLoading({
        title: '加载中',
    	mask:"true"
    });
  },
  methods: {
    getData () {
      this.$api.im_count({
        type: 2
      }).then(res => {
        uni.hideLoading();
        this.number = res.data.data.imTeamsCount
        this.list = res.data.data.imTeamsInfo
        this.isFinish = true
      })
    }
  }
}
